.messageSender {
  display: flex;
  margin-top: 30px;
  flex-direction: column;
  background-color: white;
  border-radius: 15px;
  width: 100%;
  box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
}

.messageSender__top {
  display: flex;
  border-bottom: 1px solid #eff2f5;
  padding: 15px;
}

.messageSender__top > form {
  display: flex;
  flex: 1;
}

.messageSender__top > form > input {
  outline-width: 0;
  border: none;
  padding: 5px 20px;
  margin: 0 10px;
  border-radius: 999px;
  background-color: #eff2f5;
}

.messageSender__top > form > button {
  display: none;
}

.messageSender__input {
  flex: 1;
}

.messageSender__bottom{
    display: flex;
    justify-content: space-evenly;
}

.messeageSender__option{
    display: flex;
    align-items: center;
    padding: 20px;
    color: gray;
    margin: 5px;

}

.messeageSender__option > h3{
    font-size: medium;
    margin-left: 10px;
    cursor: pointer;
}

.messeageSender__option:hover{
    background-color: #eff2f5;
    border-radius: 20px;
}