.header{
    display: flex;
    justify-content: space-between;
    padding: 15px,20px;
    position: sticky;
    background-color: white;
    z-index: 100;
    top:0;
    box-shadow: 0px 5px 8px -9px rgba(0,0,0,0.75);
}

.header__left{
    display: flex;
    justify-content: space-evenly;
    margin: 10px;
}

.header__input{
    display: flex;
    align-items: center;
    background-color: #eff2f5;
    padding: 10px;
    margin-left: 10px;
    border-radius: 999px;
}

.header__input >input{
    background-color: transparent;
    border: none;
    outline-width: 0;
}

.header__left>img {
    height: 40px;
    width: 40px;
}

.header__middle{
    display: flex;
    justify-content: center;
    flex: 1;
}

.header__option> .MuiSvgIcon-root{
    color: gray;
}

.header__option:hover > .MuiSvgIcon-root{
    color:#2e81f4
}


.header__option{
    display: flex;
    align-items: center;
    cursor: pointer;
    padding: 0px 30px;
}

.header__option--active > .MuiSvgIcon-root{
    color:#2e81f4
}

.header__option--active {
    border-bottom: 4px solid #2e81f4;
}

.header__option:hover{
    background-color: #eff2f5;
    border-radius: 10px;
    align-items: center;
    padding: 0px 30px;
    border-bottom: none;
}

.header__info{
    display: flex;
    align-items: center;
}

.header__info > h4{
    margin-left: 10px;
}

.header__right{
    display: flex;
}