.sidebarRow {
  display: flex;
  align-items: center;
  padding: 10px;
  cursor: pointer;
}

.sidebarRow:hover{
    background-color: lightgrey;
    border-radius: 10px;
}

.sidebarRow > h4{
    margin-left: 20px;
    font-weight: bold;
}

.sidebarRow > .MuiSvgIcon-root{
    color:#2e81f4;
    font-size: xx-large;
}